import React, { useState } from 'react'
import { useToggle } from '../../../../../hooks/basic/useToggle'
import { useInputs } from '../../../../../hooks/input/useInputs'
import RegistrationAccordion from '../../../Registration/RegistrationAccordion'
import { Select, MenuItem } from '@mui/material';
import { selectItemStyle } from '../../../../../components/common/Select/selectStyle';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import DatepickerSM from '../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput';


const BasicInfoEdit = ({ data }) => {
  const [ editData, setEditData, handleChange ] = useInputs(data)

  const [ expanded, Control ] = useToggle(true)

  
  return (
    <RegistrationAccordion title='기본 정보' expanded={expanded} onChange={Control}>
      <div className="flex items-center mb-[20px]">
        <InfoLabelMD className="text-[16px] flex-none w-[111.5px]">이름</InfoLabelMD>
        <InfoEditInput
          id="last_name"
          name="last_name"
          placeholder="성 입력"
          value={editData?.last_name || ''}
          onChange={handleChange}
          sx={{
            '& .MuiInputBase-input': { height: '30px', width: '100px', fontSize: '15px' },
          }}
        />
        <InfoEditInput
          id="first_name"
          name="first_name"
          placeholder="이름 입력"
          value={editData?.first_name || ''}
          onChange={handleChange}
          sx={{
            '& .MuiInputBase-input': { height: '30px', width: '234px', fontSize: '15px' },
          }}
        />
      </div>
      <div className="flex items-center mb-[20px]">
        <InfoLabelMD className="text-[16px] flex-none w-[111.5px]">생년월일</InfoLabelMD>
        <DatepickerSM
          length={160}
          sx={{ '& .MuiInputBase-root': { height: '40px', width: '334px' } }}
          id='dob'
          name='dob'
          value={editData?.dob}
          onChange={handleChange}
        ></DatepickerSM>
        <input id='dob' value={editData?.dob} name='dob' hidden readOnly/>
      </div>
      <div className="flex items-center mb-[10px]">
        <InfoLabelMD className="text-[16px] flex-none w-[111.5px]">성별</InfoLabelMD>
        <Select
          id='sex'
          name="sex"
          placeholder="성별"
          className="w-[109px] h-[35px] text-[15px]"
          defaultValue={editData?.sex || ''}
          sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
        >
          <MenuItem sx={selectItemStyle} value='1'>남자</MenuItem>
          <MenuItem sx={selectItemStyle} value='2'>여자</MenuItem>
        </Select>
      </div>
    </RegistrationAccordion>
  )
}

export default BasicInfoEdit