import React from 'react';
import { Typography } from '@mui/material';
import MySetting from '../../../../components/UI/Molecules/Setting/MySetting';

const SettingInfo = ({ itemList, ...props }) => {

  
  return (
    <>
      <div className="flex flex-col border border-solid border-[#AAAAAA] border-x-0">
        {React.Children.toArray(itemList.map((item) => 
          <MySetting id={item.id} title={item.name} {...props} />
        ))}
      </div>
    </>
  );
};

export default SettingInfo;
