import React, { useState } from 'react';
import PageContainer from '../../../components/layout/PageContainer';
import AccountInfo from './component/AccountInfo'
import ResumeInfo from './component/ResumeInfo'
import { styled } from '@mui/system';
import { ToggleButton, ToggleButtonGroup } from '@mui/material';
import PwSetting from './component/SettingInfo/PwSetting';
import EmSetting from './component/SettingInfo/EmSetting';
import PhSetting from './component/SettingInfo/PhSetting';
import { useQuery } from '@tanstack/react-query';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../../recoil/atom/login/userInfoAtom';
import { useDebug } from '../../../hooks/basic/useDebug';
import { getMyInfo } from '../../../api/user';

const StyleBtn = styled(ToggleButton)(({ theme }) => ({
  minWidth: '250px',
  height: '40px',
  borderRadius: '5px',
  backgroundColor: '#ffffff',
  borderColor: theme.button.common.border.main,
  fontSize: '17px',
  transition: '.3s all ease',
  '&.Mui-selected, &.Mui-selected:hover': {
    background: '#6a6a6a',
    color: '#ffffff',
  },
}));



const MyPage = () => {
  // recoil
  const userInfo = useRecoilValue(userInfoAtom)

  const [tab, setTab] = useState(0);
  
  const [step, setStep] = useState(0);

  const { data } = useQuery(['getMyInfo', userInfo?.id], () => getMyInfo(userInfo?.id), {
    enabled: !!userInfo?.id,
    onSuccess: (res) => {
      // console.log(res)
    }
  })

  // toggle btn
  const handleTab = (e, newAlignment) => {
    if (newAlignment !== null) {
      setTab(newAlignment);
    }
  };
  
  // useDebug(data)

  return (
    <>
      <PageContainer width="sm">
        <div className="w-full flex flex-col gap-[10px] justify-between">
          { step === 0 &&
            <div className="w-full mt-[-15px] mb-[20px]">
              <ToggleButtonGroup value={tab} exclusive onChange={handleTab}>
                <StyleBtn value={0}>계정 정보</StyleBtn>
                <StyleBtn value={1}>{ userInfo?.user_role === 'KWANJANG' ? '관장 정보' : '사범 정보' }</StyleBtn>
              </ToggleButtonGroup>
            </div>
          }

          { tab === 0 &&
          <>
            { step === 0 && data && <AccountInfo setStep={setStep} data={data} /> }
            { step === 1 && <PwSetting setStep={setStep} /> }
            { step === 2 && <EmSetting setStep={setStep} /> }
            { step === 3 && <PhSetting setStep={setStep} /> }
          </>
          }
          { tab === 1 &&
            <ResumeInfo />
          }
          
        </div>
      </PageContainer>
    </>
  );
};

export default MyPage;
