import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text'



const SabumCareer = ({ data }) => {

  const [expanded, Control] = useToggle(false)

  return (
    <RegistrationAccordion title='사범 경력' expanded={expanded} onChange={Control}>
      { (!data?.sabum_career_info || (data?.sabum_career_info?.length === 0)) && 
        <span className='text-[#7a7a7a] ml-[10px]'>없음</span>
      }
      { data?.sabum_career_info?.length > 0 &&
        React.Children.toArray(
          data?.sabum_career_info?.map((item, idx) =>
            <>
              {idx !==0 && <div className="w-full h-[1px] bg-[#eeeeee] mt-[20px] mb-[20px]"></div> }
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none ">도장명</InfoLabelMD>
                  <Text
                    width="300px"
                    value={item?.dojang_name}
                  />
                </div>
              </div>
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none">재직기간</InfoLabelMD>
                  <Text
                    width="300px"
                    value={ (item?.employment_start_year || '') + '.' + (item?.employment_start_month || '')
                      + ' ~ ' + (item?.employment_end_year || '') + '.' + (item?.employment_end_month || '')
                    }
                  />
                </div>
              </div>
            </>
      ))}
      
    </RegistrationAccordion>
  );
};

export default SabumCareer;
