import React, { useRef, useState } from 'react';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import MySettingInfo from '../../../../components/UI/Molecules/Setting/MySettingInfo';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import ProfileEditUploadBtn from '../../../../components/UI/Atoms/Icon/ProfileEditUploadBtn';
import { Typography } from '@mui/material';
import SettingInfo from './SettingInfo';
import { useMutation, useQuery } from '@tanstack/react-query';
import { updateSabumInfo } from '../../../../api/Sabum/sabum';
import { updateKwanjangInfo } from '../../../../api/Kwanjang/kwanjang';
import { useInputs } from '../../../../hooks/input/useInputs';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { getFormData } from '../../../../utils/basic/getFormData';
import { formDataDebug } from '../../../../utils/debug/debug';
import { useFileInput } from '../../../../hooks/file/useFileInput';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom';
import { getLinkedInfo } from '../../../../api/user';
import { phoneHyphenRegex } from '../../../../utils/lib/Regex';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';


const AccountInfo = ({ setStep, data }) => {
  const [editData, setEditData, handleChange] = useInputs({
    photo_url: data?.User_info?.photo_url,
    first_name: data?.User_info?.first_name,
    last_name: data?.User_info?.last_name,
    username: data?.Account_info?.username,
    email: data?.User_info?.email,
    phone_number: data?.User_info?.phone_number,
  });
  // useDebug(editData)

  const [myEdit, setMyEdit] = useState(false);

  const itemList = [
    { id: 1, name: '비밀번호 변경' },
    { id: 2, name: '이메일 변경' },
    { id: 3, name: '전화번호 변경' },
  ];


  // recoil
  const userInfo = useRecoilValue(userInfoAtom)

  // custom hook

  // query
  const { mutateAsync: getIdMT, isLoading } = useMutation(['getLinkedInfo'], () => getLinkedInfo())

  const { mutate: updateMT, isLoading: updateLD } = useMutation(async (body) => {
    const res = await getIdMT()

    if ( userInfo.user_role === 'KWANJANG' ) {
      updateKwanjangInfo(res.id, body)
    }
    if ( userInfo.user_role === 'SABUM' ) {
      updateSabumInfo(res.id, body)
    }
  })

  // const [img, setImg] = useState('')
  const [handleFileInput, fileInputRef, clickUpload] = useFileInput({
    data: editData,
    urlKey: 'previewUrl',
    fileKey: 'img',
    set: setEditData,
  });

  // handler
  const handleStep = (e) => {
    const clickItem = itemList[e.currentTarget.id - 1].id;
    setStep(clickItem);
  };

  const handleSave = () => {
    if (editData?.img) {
      const formData = getFormData([['img', editData?.img]])
      // formDataDebug(formData)
      updateMT(formData, {
        onSuccess: () => {
          window.location.reload()
        },
        onError: () => {
          alert('오류가 발생하였습니다. 잠시후 다시 시도해주세요.')
        }
      })
    } else {
      alert('이미지 파일을 업로드 해주세요.')
    }
  };

  // console.log(document.getElementById('photo_url'))
  return (
    <>
      <div className="flex flex-col gap-[30px]">
        <div className="flex justify-between items-center">
          <Typography variant="h3">계정 정보</Typography>
          {!myEdit ? (
            <EditBtnSM onClick={() => setMyEdit(true)}>편집</EditBtnSM>
          ) : (
            <div className="flex gap-[5px]">
              <EditBtnSM onClick={() => setMyEdit(false)}>취소</EditBtnSM>
              <BlackBtnSM onClick={handleSave} disabled={updateLD}>완료</BlackBtnSM>
            </div>
          )}
        </div>
        <div className="flex justify-center items-center">
          <div className="relative mt-[15px]">
            <ProfileImage
              size={140}
              id='photo_url'
              name="photo_url"
              urlKey={editData.photo_url}
              previewUrl={editData.previewUrl}
              disabled={!myEdit}
              onChange={handleFileInput}
              ref={fileInputRef}
            />
            {myEdit && (
              <ProfileEditUploadBtn className="bottom-0 right-0" onClick={clickUpload} />
            )}
          </div>
        </div>
        <div className="px-[10px] flex flex-col gap-[10px] mt-[20px] mb-[15px]">
          {/* <MySettingInfo
            title="이름"
            disabled
            value={getFullname(editData.first_name, editData.last_name)}
          /> */}
          <MySettingInfo title="ID" disabled value={editData.username} />
          <MySettingInfo title="이메일" disabled value={editData.email} />
          <MySettingInfo title="전화번호" disabled value={phoneHyphenRegex(editData.phone_number)} />
        </div>

        <SettingInfo itemList={itemList} onClick={handleStep} />
      </div>
    </>
  );
};

export default AccountInfo;
