import React, { useRef, useState } from 'react';
import { useRecoilValue } from 'recoil';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import { Typography } from '@mui/material';
import { useMutation, useQuery } from '@tanstack/react-query';
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom';
import { getAccountInfo, updateKwanjangInfo } from '../../../../api/Kwanjang/kwanjang';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { getFormData } from '../../../../utils/basic/getFormData';
import BasicInfo from './Resume/BasicInfo';
import CertificateInfo from './Resume/CertificateInfo';
import ExtraCertificationInfo from './Resume/ExtraCertificationInfo';
import DojangCareer from './Resume/DojangCareer';
import BasicInfoEdit from './Resume/BasicInfoEdit';
import CertificateInfoEdit from './Resume/CertificateInfoEdit';
import ExtraCertificationInfoEdit from './Resume/ExtraCertificationInfoEdit';
import ExtraCertificationInfoSabumEdit from './Resume/ExtraCertificationInfoSabumEdit';
import DojangCareerEdit from './Resume/DojangCareerEdit';
import GraduationInfo from './Resume/GraduationInfo';
import DriverInfo from './Resume/DriverInfo';
import SabumCareer from './Resume/SabumCareer';
import AwardHistory from './Resume/AwardHistory';
import ExtraCertificationInfoSabum from './Resume/ExtraCertificationInfoSabum';
import GraduationInfoEdit from './Resume/GraduationInfoEdit';
import SabumCareerEdit from './Resume/SabumCareerEdit';
import DriverInfoEdit from './Resume/DriverInfoEdit';
import { getAccountInfoSabum, updateSabumInfo } from '../../../../api/Sabum/sabum';
import { getLinkedInfo } from '../../../../api/user';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';
import { formDataDebug } from '../../../../utils/debug/debug';

const ResumeInfo = () => {
  const [edit, setEdit] = useState(false);

  const handleEditComplete = () => {
    setEdit(true);
  };

  const handleCancelEdit = () => {
    setEdit(false);
  };

  // recoil
  const userInfo = useRecoilValue(userInfoAtom);
  const roleId = useRef('');

  // query - getInfo
  const { data } = useQuery(
    ['getResumeInfo', userInfo?.id],
    () => {
      if (userInfo?.user_role === 'KWANJANG') {
        return getAccountInfo(userInfo?.id);
      }
      if (userInfo?.user_role === 'SABUM') {
        return getAccountInfoSabum(userInfo?.id);
      }
      return '';
    },
    {
      enabled: !!userInfo?.id,
    },
  );

  // query - getKwanjangId or SabumId
  const { mutateAsync: getIdMT } = useMutation(() => getLinkedInfo());

  // query - updateInfo
  const { mutate: updateMT, isLoading: updateLD } = useMutation(async (body) => {
    const res = await getIdMT();
    if (userInfo.user_role === 'KWANJANG') {
      updateKwanjangInfo(res.id, body);
    }
    if (userInfo.user_role === 'SABUM') {
      updateSabumInfo(res.id, body);
    }
  });

  // useDebug(data)

  // handler
  const handleSubmit = (e) => {
    /////// kwanjang info update ///////

    if (userInfo?.user_role === 'KWANJANG') {
      // basicInfo
      let last_name = document.getElementById('last_name')?.value;
      let first_name = document.getElementById('first_name')?.value;
      let dob = document.getElementById('dob')?.value;
      let sex =
        document.getElementById('sex').textContent == '남자'
          ? '1'
          : document.getElementById('sex').textContent == '여자'
          ? '2'
          : '';

      // certificates
      let level = document.getElementById('level')?.textContent;
      let tkd_level_number = document.getElementById('tkd_level_number')?.value;
      let instructor_license_grade = document.getElementById(
        'instructor_license_grade',
      )?.textContent;
      let instructor_license_number = document.getElementById('instructor_license_number')?.value;

      // otherCertificates
      let otherCertLength = document.getElementsByName('license_name')?.length;
      let otherCertList = [];
      for (let i = 0; i < otherCertLength; i++) {
        let temp = {
          license_name: document.getElementsByName('license_name')[i]?.value,
          license_grade: document.getElementsByName('license_grade')[i]?.value,
          license_number: document.getElementsByName('license_number')[i]?.value,
        };
        otherCertList.push(temp);
      }

      // career
      let careerLength = document.getElementsByName('dojang_name')?.length;
      let careerList = [];
      for (let i = 0; i < careerLength; i++) {
        let startDate = document.getElementsByName('employment_start_date')[i]?.value;
        let endDate = document.getElementsByName('employment_end_date')[i]?.value;

        const temp = {
          dojang_name: document.getElementsByName('dojang_name')[i]?.value,
          employment_start_year: startDate?.slice(0, 4),
          employment_start_month: Number(startDate?.slice(5, 7)),
          employment_end_year: endDate?.slice(0, 4),
          employment_end_month: Number(endDate?.slice(5, 7)),
        };
        careerList.push(temp);
      }

      // driverLicense
      let driver_license = document.getElementById('driver_license')?.value;
      let driver_license_obtaining_date = document.getElementById(
        'driver_license_obtaining_date',
      )?.value;

      if (userInfo?.id) {
        let temp = {
          user: userInfo?.id,
          last_name: last_name,
          first_name: first_name,
          dob: dob,
          sex: sex,
          level: level,
          tkd_level_number: tkd_level_number,
          instructor_license_grade: instructor_license_grade,
          instructor_license_number: instructor_license_number,
        };

        const data = [];
        data.push(...Object.entries({ ...temp }));

        if (driver_license && driver_license_obtaining_date) {
          let date = driver_license_obtaining_date;
          let driver_license_obtaining_year = date.slice(0, 4);
          let driver_license_obtaining_month = Number(date.slice(5, 7));
          data.push(['driver_license', driver_license]);
          data.push(['driver_license_obtaining_year', driver_license_obtaining_year]);
          data.push(['driver_license_obtaining_month', driver_license_obtaining_month]);
        }

        if (careerList.length != 0) {
          data.push(['kwanjang_career_info', JSON.stringify(careerList)]);
        }

        if (otherCertList.length != 0) {
          data.push(['kwanjang_license_info', JSON.stringify(otherCertList)]);
        }

        const formData = getFormData(data);
        // formDataDebug(formData)

        updateMT(formData, {
          onSuccess: (res) => {
            setLoading(true);
            setTimeout(window.location.reload(), 500);
            return;
          },
          onError: (err) => {
            alert(err.response.data.message);
            return;
          },
        });
        return;
      }
    }

    //////// sabum info update ///////

    if (userInfo?.user_role === 'SABUM') {
      // basicInfo
      let last_name = document.getElementById('last_name')?.value;
      let first_name = document.getElementById('first_name')?.value;
      let dob = document.getElementById('dob')?.value;
      let sex =
        document.getElementById('sex').textContent == '남자'
          ? '1'
          : document.getElementById('sex').textContent == '여자'
          ? '2'
          : '';

      // univInfo
      let school_name = document.getElementById('school_name')?.value;
      let admission_date = document.getElementById('admission_date')?.value;
      let admission_year = admission_date?.slice(0, 4);
      let admission_month = Number(admission_date?.slice(5, 7));
      let graduated_date = document.getElementById('graduated_date')?.value;
      let graduated_year = graduated_date?.slice(0, 4);
      let graduated_month = Number(graduated_date?.slice(5, 7));
      let major = document.getElementById('major')?.value;
      let grades = document.getElementById('grades')?.value;
      let total_grades = document.getElementById('total_grades')?.textContent;

      // certificate
      let level = document.getElementById('level')?.textContent;
      let tkd_level_number = document.getElementById('tkd_level_number')?.value;
      let instructor_license_grade = document.getElementById(
        'instructor_license_grade',
      )?.textContent;
      let instructor_license_number = document.getElementById('instructor_license_number')?.value;

      // extraCertification
      let otherCertLength = document.getElementsByName('license_name')?.length;
      let otherCertList = [];
      for (let i = 0; i < otherCertLength; i++) {
        let temp = {
          license_name: document.getElementsByName('license_name')[i]?.value,
          license_grade: document.getElementsByName('license_grade')[i]?.value,
          license_number: document.getElementsByName('license_number')[i]?.value,
        };
        otherCertList.push(temp);
      }

      // CareerType
      let careerLength = document.getElementsByName('dojang_name')?.length;
      let careerList = [];
      for (let i = 0; i < careerLength; i++) {
        let startDate = document.getElementsByName('employment_start_date')[i]?.value;
        let endDate = document.getElementsByName('employment_end_date')[i]?.value;

        let temp = {
          dojang_name: document.getElementsByName('dojang_name')[i]?.value,
          employment_start_year: startDate?.slice(0, 4),
          employment_start_month: Number(startDate?.slice(5, 7)),
          employment_end_year: endDate?.slice(0, 4),
          employment_end_month: Number(endDate?.slice(5, 7)),
        };
        careerList.push(temp);
      }

      // driverLicense
      let driver_license = document.getElementById('driver_license')?.value;
      let driver_license_obtaining_date = document.getElementById(
        'driver_license_obtaining_date',
      )?.value;

      if (userInfo?.id) {
        let temp = {
          user: userInfo?.id,
          last_name: last_name,
          first_name: first_name,
          dob: dob,
          sex: sex,
          school_name: school_name,
          admission_year: admission_year,
          admission_month: admission_month,
          graduated_year: graduated_year,
          graduated_month: graduated_month,
          major: major,
          grades: grades,
          total_grades: total_grades,
          level: level,
          tkd_level_number: tkd_level_number,
          instructor_license_grade: instructor_license_grade,
          instructor_license_number: instructor_license_number,
        };

        const data = [];
        data.push(...Object.entries(temp));

        if (driver_license && driver_license_obtaining_date) {
          let date = driver_license_obtaining_date;
          let driver_license_obtaining_year = date.slice(0, 4);
          let driver_license_obtaining_month = Number(date.slice(5, 7));
          data.push(['driver_license', driver_license]);
          data.push(['driver_license_obtaining_year', driver_license_obtaining_year]);
          data.push(['driver_license_obtaining_month', driver_license_obtaining_month]);
        }

        if (careerList.length != 0) {
          data.push(['sabum_career_info', JSON.stringify(careerList)]);
        }

        if (otherCertList.length != 0) {
          data.push(['sabum_license_info', JSON.stringify(otherCertList)]);
        }

        // console.log(data)
        const formData = getFormData(data);
        // formDataDebug(formData)

        updateMT(formData, {
          onSuccess: (res) => {
            setLoading(true);
            // setTimeout(window.location.reload(), 500)
          },
          onError: (err) => {
            alert(err.response.data.message);
            return;
          },
        });
      }
    } else {
      alert('오류가 발생하였습니다.');
      return;
    }
  };

  const [loading, setLoading] = useState(false);

  // useDebug(data)
  return (
    <>
      <div className="flex flex-col gap-[30px]">
        <div className="">
          <div className="flex justify-between items-center">
            <Typography variant="h3">
              {userInfo?.user_role === 'KWANJANG' ? '관장 정보' : '사범 정보'}
            </Typography>
            {!edit ? (
              <EditBtnSM onClick={() => setEdit(true)}>편집</EditBtnSM>
            ) : (
              <div className="flex gap-[5px]">
                <EditBtnSM onClick={handleCancelEdit}>취소</EditBtnSM>
                <BlackBtnSM onClick={handleSubmit} disabled={loading || updateLD}>
                  완료
                </BlackBtnSM>
              </div>
            )}
          </div>
          <div className="w-[500px] mt-[20px]">
            {!edit && userInfo?.user_role === 'KWANJANG' && (
              <>
                <BasicInfo data={data} />
                <GraduationInfo data={data} />
                <CertificateInfo data={data} />
                <ExtraCertificationInfo data={data} />
                <DojangCareer data={data} />
                <AwardHistory data={data} />
                <DriverInfo data={data} />
              </>
            )}
            {!edit && userInfo?.user_role === 'SABUM' && (
              <>
                <BasicInfo data={data} />
                <GraduationInfo data={data} />
                <CertificateInfo data={data} />
                <ExtraCertificationInfoSabum data={data} />
                <SabumCareer data={data} />
                <AwardHistory data={data} />
                <DriverInfo data={data} />
              </>
            )}
            {edit && userInfo?.user_role === 'KWANJANG' && (
              <>
                <BasicInfoEdit data={data} />
                <GraduationInfoEdit data={data} />
                <CertificateInfoEdit data={data} />
                <ExtraCertificationInfoEdit data={data} />
                <DojangCareerEdit data={data} />
                <DriverInfoEdit data={data} />
              </>
            )}
            {edit && userInfo?.user_role === 'SABUM' && (
              <>
                <BasicInfoEdit data={data} />
                <GraduationInfoEdit data={data} />
                <CertificateInfoEdit data={data} />
                <ExtraCertificationInfoSabumEdit data={data} />
                <SabumCareerEdit data={data} />
                <DriverInfoEdit data={data} />
              </>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default ResumeInfo;
