import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text'



const ExtraCertificationInfoSabum = ({ data }) => {

  const [expanded, Control] = useToggle(false)


  return (
    <RegistrationAccordion title='기타 단증' expanded={expanded} onChange={Control}>
      { (!data?.sabum_license_info || (data?.sabum_license_info?.length === 0)) && 
        <span className='text-[#7a7a7a] ml-[10px]'>없음</span>
      }
      { data?.sabum_license_info?.length > 0 &&
        React.Children.toArray(
          data?.sabum_license_info?.map((item, idx) =>
            <>
              {idx !==0 && <div className="w-full h-[1px] bg-[#eeeeee] mt-[20px] mb-[20px]"></div> }
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none ">자격증 이름</InfoLabelMD>
                  <Text
                    width="300px"
                    value={item?.license_name}
                  />
                </div>
              </div>
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none">급</InfoLabelMD>
                  <Text
                    width="300px"
                    value={item?.license_grade}
                  />
                </div>
              </div>
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none">일련번호</InfoLabelMD>
                  <Text
                    width="300px"
                    value={item?.license_number}
                  />
                </div>
              </div>
            </>
          )
        )
      }

    </RegistrationAccordion>
  );
};

export default ExtraCertificationInfoSabum;
