import React from 'react'
import { useForm } from 'react-hook-form'
import { Typography } from '@mui/material'
import PageContainer from '../../../../../components/layout/PageContainer'
import ValidationEmail from '../../../../../components/UI/Atoms/Input/Validation/ValidationEmail'
import InputCover from '../../../../../components/UI/Atoms/Input/InputCover'
import EditBtnMD from '../../../../../components/UI/Atoms/Button/EditBtnMD'
import PrimaryBtnMD from '../../../../../components/UI/Atoms/Button/PrimaryBtnMD'
import { useMutation } from '@tanstack/react-query'
import { changeEmail } from '../../../../../api/user'



const EmSetting = ({ setStep }) => {

  // const {
  //   handleSubmit,
  //   register,
  //   formState: { errors },
  // } = useForm();

  // const onSubmit = (data) => console.log(data);
  const { mutate: emailChange } = useMutation((body) => changeEmail(body))

  const handleSubmit = (e) => {
    e.preventDefault();

    const newEmail = document.getElementById('email').value;
    
    let body = { email: newEmail }

    emailChange(body, {
      onSuccess: (res) => {
        if(res == 'MyInfo is successfully updated')
        alert('이메일이 변경되었습니다.')
        setStep(0)
        window.location.reload()
      }
    })
  };

  return (
    <>
      <PageContainer width="sm">
        <div className="w-[500px] flex flex-col gap-[10px] justify-between">
          <Typography variant="h2">이메일 변경</Typography>
          <form onSubmit={handleSubmit}>
            <div className="flex flex-col gap-[20px] mt-[64px]">
              <ValidationEmail 
              id='email'
              label="새 이메일 입력" />
            </div>
            <div className="flex justify-center my-[30px] gap-[10px]">
              <EditBtnMD onClick={() => setStep(0)} className="text-[#5E81F4] border-[#cccccc]">
                취소
              </EditBtnMD>
              <PrimaryBtnMD type="submit" className="text-[16px]">
                저장하기
              </PrimaryBtnMD>
            </div>
          </form>
        </div>
      </PageContainer>
    </>
  );
}

export default EmSetting
