import React from 'react';
import PageContainer from '../../../../../components/layout/PageContainer';
import { Typography } from '@mui/material';
import InputCover from '../../../../../components/UI/Atoms/Input/InputCover';
import EditBtnMD from '../../../../../components/UI/Atoms/Button/EditBtnMD';
import PrimaryBtnMD from '../../../../../components/UI/Atoms/Button/PrimaryBtnMD';
import { useMutation } from '@tanstack/react-query';
import { changePW } from '../../../../../api/user';

const PwSetting = ({ setStep }) => {

  const { mutate: pwChange } = useMutation((body) => changePW(body));

  const handleSubmit = (e) => {
    e.preventDefault();

    const currPw = document.getElementById('current_password').value;
    const newPw = document.getElementById('new_password').value;
    const checkPw = document.getElementById('pw_check').value;

    let body = {
      current_password: currPw,
      new_password: newPw,
    };
    if (currPw == newPw) {
      alert('동일한 비밀번호는 사용할 수 없습니다.')
    }

    if (newPw != checkPw) {
      alert('새로운 비밀번호가 일치하지 않습니다.');
    }

    if ((currPw != newPw) && (newPw == checkPw)) {
      pwChange(body, {
        onSuccess: (res) => {
          if (res == 'wrong password'){
            alert('현재 비밀번호가 일치하지 않습니다.')
          }
          if (res == 'MyInfo is successfully updated') {
            alert('비밀번호가 변경되었습니다.');
            setStep(0);
            window.location.reload()
          }
        },
      });
    }
  };

  return (
    <>
      <PageContainer width="sm">
        <div className="w-[500px] flex flex-col gap-[10px] justify-between">
          <Typography variant="h2">비밀번호 재설정</Typography>
          <form onSubmit={handleSubmit}>
            <div className="flex flex-col mt-[64px]">
              <InputCover
                id="current_password"
                type="password"
                name="current_password"
                label="현재 비밀번호"
                helperText='현재 비밀번호를 입력해주세요.'
                className="mb-[10px]"
              />
              <InputCover
                id="new_password"
                type="password"
                name="new_password"
                label="새 비밀번호"
                helperText='새 비밀번호를 입력해주세요.'
                className="mb-[10px]"
              />
              <InputCover
                id="pw_check"
                type="password"
                name="pw_check"
                label="새 비밀번호 확인"
                helperText='비밀번호를 한번 더 입력해주세요.'
                className="mb-[10px]"
              />
            </div>
            <div className="flex justify-center my-[30px] gap-[10px]">
              <EditBtnMD onClick={() => setStep(0)} className="text-[#5E81F4] border-[#cccccc]">
                취소
              </EditBtnMD>
              <PrimaryBtnMD type="submit" className="text-[16px]">
                저장하기
              </PrimaryBtnMD>
            </div>
          </form>
        </div>
      </PageContainer>
    </>
  );
};

export default PwSetting;
