import { MenuItem, Select, Typography } from '@mui/material';
import React, { useMemo, useState } from 'react';
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import { Input } from '../../../Manage/Student/component/StudentInfo/StudentForm/StudentForm';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import Text from './component/Text'

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};


const CertificateInfo = ({ data }) => {

  // handler
  const [ expanded, Control ] = useToggle(false)

  return (
    <RegistrationAccordion title='단증 & 사범 자격증' expanded={expanded} onChange={Control}>
      <div className="mb-[20px]">
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">태권도 보유 단증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Text
            width="20%"
            value={data?.level}
          />
          <Text
            width="80%"
            value={data?.tkd_level_number && '[단증번호]   ' + (data?.tkd_level_number || '' )}
          />
        </div>
      </div>
      <div>
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">사범 자격증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Text
            width="20%"
            value={data?.instructor_license_grade}
          />
          <Text
            width="80%"
            value={data?.instructor_license_number && '[자격증번호]   ' + (data?.instructor_license_number || '' )}
          />
        </div>
      </div>
    </RegistrationAccordion>
  );
};
export default CertificateInfo;
