import React from 'react'
import { styled } from '@mui/system';
import { Box } from '@mui/material'
import { FormControl, OutlinedInput } from '@mui/material';
import InfoLabelMD from '../../Atoms/Info/InfoLabelMD'

const StyledFormControl = styled(FormControl)(({ theme }) => ({
    color: '#000000',
    width: '100%',
  '& .MuiOutlinedInput-root': {
    padding: '0 10px 0 0',
    fontSize: theme.font.size.m,
    fontWeight: theme.font.weight.regular,
    height: '30px',
    background: '#ffffff',
    color: '#000000',
    letterSpacing: 'normal',
  },
  '& .MuiOutlinedInput-notchedOutline': {
    border: '1px solid #AAAAAA',
    padding: '5px 10px',
  },
  '& .Mui-disabled': {
    'WebkitTextFillColor': '#000000!important',
    '.MuiOutlinedInput-notchedOutline': {
    border: 'none',
    },
  },
  '& .Mui-focused .MuiOutlinedInput-notchedOutline': {
    border: '1px solid #000000',
  },
}))


const MySettingInfo = ({title, ...props}) => {
  return (
    <>
      <Box className="flex justify-between items-center h-[40px]">
        <InfoLabelMD className='text-[16px]'>{title}</InfoLabelMD>
        <Box sx={{ width: 334, borderBottom: 1, borderColor: '#DDDDDD', gap: '10px', }}>
          <StyledFormControl>
            <OutlinedInput {...props} />
          </StyledFormControl>
        </Box>
      </Box>
    </>
  )
}

export default MySettingInfo