import React, { useState } from 'react'
import { useToggle } from '../../../../../hooks/basic/useToggle'
import RegistrationAccordion from '../../../Registration/RegistrationAccordion'
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import DatepickerSM from '../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput';
import CareerBtn from '../../../../../components/UI/Atoms/ButtonGroup/CareerBtn';
import CreateBtnMD from '../../../../../components/UI/Atoms/Button/CreateBtnMD';
import BlackBtnSM from '../../../../../components/UI/Atoms/Button/BlackBtnSM';

const DojangCareerEdit = ({ data }) => {
  const init = data?.kwanjang_career_info?.map(item => {
    let temp = {
      dojang_name: item?.dojang_name,
      employment_start_date: item?.employment_start_year + '-' + item?.employment_start_month + '-' + '01',
      employment_end_date: item?.employment_end_year + '-' + item?.employment_end_month + '-' + '02',
    }
    return temp
  }) || []

  const [ editData, setEditData ] = useState(init)
  const form = { dojang_name: '', employment_start_date: '', employment_end_date: '' };

  // handler
  const handleAdd = (e) => {
    setEditData([...editData, form]);
  };

  const handleDelete = (e) => {
    let { id } = e.currentTarget;
    let temp = editData.filter((v, idx) => {
      return id != idx;
    });
    setEditData(temp);
  };

  const handleChange = (e) => {
    let { id, name, value } = e.currentTarget;
    let temp = [...editData];
    temp[id] = { ...temp[id], [name]: value };
    setEditData(temp);
  };

  const handleSelect = (e, idx) => {
    let { name, value } = e.target;
    let temp = [...editData];
    temp[idx] = { ...temp[idx], [name]: value };
    setEditData(temp);
  };

  const [ expanded, Control ] = useToggle()
  const [ alignment, setAlignment ] = useState(data?.kwanjang_career_info?.length > 0 ? true : false)

  return (
    <RegistrationAccordion title='도장 운영 경력' expanded={expanded} onChange={Control}>
      <div>
        <div className="mb-[30px]">
          <CareerBtn alignment={alignment} setAlignment={setAlignment}/>
        </div>
        { alignment &&
           React.Children.toArray(
            editData?.map((item, idx) => 
            <div className='mb-[20px]'>
              {idx !== 0 && (
                <>
                  <div className="w-full h-[1px] bg-[#aaaaaa] mt-[10px] mb-[20px]"></div>
                  <div className="text-right mb-[10px]">
                    <BlackBtnSM id={`${idx}`} className="mb-[10px]" onClick={handleDelete}>
                      삭제
                    </BlackBtnSM>
                  </div>
                </>
              )}
              <div className="flex flex-col gap-[10px]">
                <div className="flex items-center justify-between">
                  <InfoLabelMD className="text-[16px] flex-none">도장명</InfoLabelMD>
                  <InfoEditInput
                    placeholder="도장명 입력"
                    sx={{ '& .MuiInputBase-input': { height: '30px', width: '334px' } }}
                    id={`${idx}`}
                    name='dojang_name'
                    value={item.dojang_name}
                    onChange={handleChange}
                  ></InfoEditInput>
                </div>
                <div className="flex items-center justify-between">
                  <InfoLabelMD className="text-[16px] w-[100px] flex-none">운영기간</InfoLabelMD>
                  <DatepickerSM
                    length={160}
                    sx={{ '& .MuiInputBase-root': { height: '40px', } }}
                    id={`${idx}`}
                    name='employment_start_date'
                    value={item.employment_start_date}
                    onChange={(e) => handleSelect(e,idx)}
                  ></DatepickerSM>
                  <input value={item.employment_start_date} name='employment_start_date' hidden readOnly/>
                  <p className="m-0">~</p>
                  <DatepickerSM 
                    length={160}
                    id={`${idx}`}
                    name='employment_end_date'
                    value={item.employment_end_date}
                    onChange={(e) => handleSelect(e,idx)}
                  ></DatepickerSM>
                  <input value={item.employment_end_date} name='employment_end_date' hidden readOnly/>
                </div>
              </div>
            </div>
            )
          )
        }
        { alignment &&
          <div className="text-center mt-[30px]">
            <CreateBtnMD className="w-[115px] text-[14px]" onClick={handleAdd}>
              + 경력 추가
            </CreateBtnMD>
          </div>
        }
        </div>
    </RegistrationAccordion>
  )
}

export default DojangCareerEdit