import React, { useState } from 'react';
import PageContainer from '../../../../../components/layout/PageContainer';
import { Typography } from '@mui/material';
import EditBtnMD from '../../../../../components/UI/Atoms/Button/EditBtnMD';
import { useMutation } from '@tanstack/react-query';
import { changePhone, sendSMSforId } from '../../../../../api/user';
import InputCover from '../../../../../components/UI/Atoms/Input/InputCover';
import PrimaryBtnSM from '../../../../../components/UI/Atoms/Button/PrimaryBtnSM';

const PhSetting = ({ setStep }) => {
  
  const regPhone = new RegExp(/^\d{11}$/);

  const [phone_number, setPhoneNumber] = useState('');
  
  const { mutate: SMSMT } = useMutation((phone_number) => sendSMSforId(phone_number));

  const { mutate: phChange } = useMutation((body) => changePhone(body));

  const handlePhoneNumber = () => {

    const phone = document.getElementById('phone_number').value;

    if ((phone.length >= 11) && (regPhone.test(phone) == true)) {
      SMSMT(phone, {
        onSuccess: (res) => {
          alert('인증번호 전송완료');
          console.log(res);
        },
      });
      setPhoneNumber(phone);
    } 
    if ((phone.length < 11) || (regPhone.test(phone) == false)) {
      alert('휴대폰 번호를 다시 확인해주세요.');
    }
  };

  const handleSms = () => {
    const num = document.getElementById('sms').value;

    const inputData = {
      new_phone_number: phone_number,
      number_by_user: num,
    };
    console.log(inputData)
    phChange(inputData, {
      onSuccess: (res) => {
        console.log(res)
        if (res == 'wrong access or expired') {
          alert('인증번호를 다시 확인해주세요.');
        } 
        else {
          setPhoneNumber(res);
          alert('휴대폰 번호가 변경되었습니다.')
          setStep(0);
          window.location.reload();
        }
      },
    });
  };



  return (
    <>
      <PageContainer width="sm">
        <div className="w-[500px] flex flex-col justify-between">
          <Typography variant="h2">전화번호 변경</Typography>
              <div className="flex items-baseline gap-[10px] mb-[20px] mt-[64px]">
                <InputCover
                  type='tel'
                  id="phone_number"
                  label="전화번호"
                  name="phone_number"
                  variant="outlined"
                  fullWidth
                  placeholder="전화번호를 입력해주세요."
                  inputProps={{ maxLength: 11, type: 'tel' }}
                />
                <PrimaryBtnSM onClick={handlePhoneNumber} sx={{ height: 50 }}>
                  인증요청
                </PrimaryBtnSM>
              </div>
              <div className="flex items-baseline gap-[10px]">
                <InputCover
                  id="sms"
                  label="인증번호"
                  name="sms"
                  variant="outlined"
                  fullWidth
                  placeholder="인증번호를 입력해주세요."
                  inputProps={{ maxLength: 4 }}
                />
                <PrimaryBtnSM onClick={handleSms} sx={{ height: 50 }}>
                  인증확인
                </PrimaryBtnSM>
              </div>
              <div className="flex justify-center my-[30px] gap-[10px]">
                <EditBtnMD onClick={() => setStep(0)} className="text-[#5E81F4] border-[#cccccc]">
                  취소
                </EditBtnMD>
              </div>
            </div>
      </PageContainer>
    </>
  );
};

export default PhSetting;
