import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text'
import { getFullname } from '../../../../../utils/render/getFullname';

const GraduationInfo = ({ data }) => {
  
  const [expanded, Control] = useToggle(false)

  return (
    <RegistrationAccordion title='최종 학력' expanded={expanded} onChange={Control}>
      <div className="mb-[20px]">
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none">학교명</InfoLabelMD>
          <Text
            width="300px"
            value={data?.school_name}
          />
        </div>
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none">재학 기간</InfoLabelMD>
          <Text
            width="300px"
            value={(data?.admission_year || '') + '-' + (data?.admission_month || '') + ' ~ ' + (data?.graduated_year || '') + '-' + (data?.graduated_month || '')}
          />
        </div>
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none ">전공</InfoLabelMD>
          <Text
            width="300px"
            value={data?.major}
          />
        </div>
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none ">학점</InfoLabelMD>
          <Text
            width="300px"
            value={(data?.grades || '') + ' / ' + (data?.total_grades || '')}
          />
        </div>
      </div>
    </RegistrationAccordion>
  )
}

export default GraduationInfo