import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text'
import { getFullname } from '../../../../../utils/render/getFullname';
import { toGender } from '../../../../../utils/render/toGender';

const BasicInfo = ({ data }) => {
  
  const [expanded, Control] = useToggle(true)

  return (
    <RegistrationAccordion title='기본 정보' expanded={expanded} onChange={Control}>
      <div className="mb-[20px]">
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none">이름</InfoLabelMD>
          <Text
            width="300px"
            value={getFullname(data?.last_name, data?.first_name)}
          />
        </div>
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none">생년월일</InfoLabelMD>
          <Text
            width="300px"
            value={data?.dob}
          />
        </div>
        <div className="flex items-center justify-between mb-[15px]">
          <InfoLabelMD className="text-[16px] flex-none ">성별</InfoLabelMD>
          <Text
            width="300px"
            value={toGender(data?.sex)}
          />
        </div>
      </div>
    </RegistrationAccordion>
  )
}

export default BasicInfo