import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text';

const AwardHistory = ({ data }) => {
  const [expanded, Control] = useToggle(false);

  return (
    <RegistrationAccordion title="수상 경력" expanded={expanded} onChange={Control}>
      {(!data?.kwanjang_career_info || data?.kwanjang_career_info?.length === 0) && (
        <span className="text-[#7a7a7a] ml-[10px]">없음</span>
      )}
      {data?.kwanjang_career_info?.length > 0 &&
        React.Children.toArray(
          data?.kwanjang_career_info?.map((item, idx) => (
            <>
              {idx !== 0 && <div className="w-full h-[1px] bg-[#eeeeee] mt-[20px] mb-[20px]"></div>}
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none ">수상 내역</InfoLabelMD>
                  <Text width="300px" value={item?.dojang_name} />
                </div>
              </div>
              <div className="mb-[20px]">
                <div className="flex items-center justify-between mb-[10px]">
                  <InfoLabelMD className="text-[16px] flex-none">수상 날짜</InfoLabelMD>
                  <Text
                    width="300px"
                    value={
                      (item?.employment_start_year || '') +
                      '.' +
                      (item?.employment_start_month || '') +
                      ' ~ ' +
                      (item?.employment_end_year || '') +
                      '.' +
                      (item?.employment_end_month || '')
                    }
                  />
                </div>
              </div>
            </>
          )),
        )}
    </RegistrationAccordion>
  );
};

export default AwardHistory;
