import React, { useState, useEffect } from 'react';
import InputCover from '../InputCover';
import { emailRegex } from '../../../../../utils/lib/Regex';

const ValidationEmail = ({ ...props }) => {
  const [email, setEmail] = useState('');

  const [emailValid, setEmailValid] = useState(false);

  const [emailValidMsg, setEmailValidMsg] = useState('');

  const emailValidation = (e) => {
    const { value } = e.target;
    setEmail(value);
    if (emailRegex.test(value)) {
      setEmailValid(true);
      setEmailValidMsg('사용할 수 있는 이메일입니다.');
    } else {
      setEmailValid(false);
      setEmailValidMsg('이메일 형식이 올바르지 않습니다.');
    }
  };

  useEffect(() => {
    if (email === '') {
      setEmailValidMsg('');
    }
  }, [email]);


  return (
    <>
      <InputCover
        type='email'
        label={"이메일" || props.label}
        variant="outlined"
        value={email}
        onChange={emailValidation}
        helperText={emailValidMsg || props.helperText}
        error={!emailValid && emailValidMsg !== ''}
        fullWidth
        placeholder='이메일을 입력해주세요.'
        {...props}
      />
    </>
  );
};

export default ValidationEmail;
