import React, { useMemo } from 'react'
import { useToggle } from '../../../../../hooks/basic/useToggle'
import { useInputs } from '../../../../../hooks/input/useInputs'
import RegistrationAccordion from '../../../Registration/RegistrationAccordion'
import { Select, MenuItem, Typography } from '@mui/material';
import { selectItemStyle } from '../../../../../components/common/Select/selectStyle';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import DatepickerSM from '../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput';

const CertificateInfoEdit = ({ data }) => {
  const [ editData, setEditData, handleChange ] = useInputs(data)

  const danList = useMemo(() => [
    { id:'1단', title:'1단' },
    { id:'2단', title:'2단' },
    { id:'3단', title:'3단' },
    { id:'4단', title:'4단' },
    { id:'5단', title:'5단' },
    { id:'6단', title:'6단' },
    { id:'7단', title:'7단' },
    { id:'8단', title:'8단' },
    { id:'9단', title:'9단' },
  ])

  const geubList = useMemo(() => [
    { id:'1급', title:'1급'},
    { id:'2급', title:'2급'},
    { id:'3급', title:'3급'},
  ])

  const  [expanded, Control ] = useToggle()

  return (
    <RegistrationAccordion title='단증 & 사범 자격증' expanded={expanded} onChange={Control}>
      <div className="mb-[20px]">
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">태권도 보유 단증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Select
            className="w-[20%] h-[40px] text-[15px]"
            placeholder="단"
            id='level'
            name='level'
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
            defaultValue={editData?.level || ''}
          >
            { React.Children.toArray(
              danList.map(item =>
                <MenuItem sx={selectItemStyle} value={item.id}>
                  {item.title}
                </MenuItem>
              )
            )}
          </Select>
          <InfoEditInput
            className="w-[80%]"
            placeholder={'단증 번호 입력'}
            sx={{ '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' } }}
            id='tkd_level_number'
            name='tkd_level_number'
            defaultValue={editData?.tkd_level_number}
          ></InfoEditInput>
        </div>
      </div>
      <div>
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">사범 자격증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Select
            className="w-[20%] h-[40px] text-[15px]"
            placeholder="급"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
            id='instructor_license_grade'
            name='instructor_license_grade'
            defaultValue={editData?.instructor_license_grade || ''}
          >
            { React.Children.toArray(
              geubList.map(item =>
                <MenuItem sx={selectItemStyle} value={item.id}>
                  {item.title}
                </MenuItem>
              )
            )}
          </Select>
          <InfoEditInput
            className="w-[80%]"
            placeholder={'자격증 번호 입력'}
            id='instructor_license_number'
            name='instructor_license_number'
            sx={{ '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' } }}
            defaultValue={editData?.instructor_license_number}
          ></InfoEditInput>
        </div>
      </div>
    </RegistrationAccordion>
  )
}

export default CertificateInfoEdit