import React from 'react'
import { styled } from '@mui/system';
import { Box } from '@mui/material'
import InfoLabelMD from '../../Atoms/Info/InfoLabelMD'
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';

const BoxStyled = styled(Box)(({ theme }) => ({
  backgroundColor: '#fff',
  cursor: 'pointer',
  padding: '0 10px',
  borderTop: '1px solid #e5e5e5',
  borderBottom: '1px solid #e5e5e5',
  '&:hover': {
    backgroundColor: theme.palette.grey[100],
  },
}))


const MySettingInfo = ({...props}) => {


  return (
    <>
      <BoxStyled className="flex justify-between items-center h-[50px]" {...props}>
        {props.title && <InfoLabelMD className='text-[16px]'>{props.title}</InfoLabelMD>}
        <ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem', color: '#545454' }} />
      </BoxStyled>
    </>
  )
}

export default MySettingInfo