import React from 'react';
import RegistrationAccordion from '../../../Registration/RegistrationAccordion';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import Text from './component/Text'
import { getFullname } from '../../../../../utils/render/getFullname';

const DriverInfo = ({ data }) => {
  
  const [expanded, Control] = useToggle(false)

  return (
    <RegistrationAccordion title='운전 면허' expanded={expanded} onChange={Control}>
      { !data?.driver_license && 
        <span className='text-[#7a7a7a] ml-[10px]'>없음</span>
      }
      { !!data?.driver_license &&
        <div className="mb-[20px]">
          <div className="flex items-center justify-between mb-[15px]">
            <InfoLabelMD className="text-[16px] flex-none">운전면허</InfoLabelMD>
            <Text
              width="300px"
              value={data?.driver_license}
            />
          </div>
          <div className="flex items-center justify-between mb-[15px]">
            <InfoLabelMD className="text-[16px] flex-none">취득일</InfoLabelMD>
            <Text
              width="300px"
              value={(data?.driver_license_obtaining_year || '') + '.' + (data?.driver_license_obtaining_month || '')}
            />
          </div>
        </div>
      }
    </RegistrationAccordion>
  )
}

export default DriverInfo